body{
    margin:0;
    padding:0;
}
.form{
   border-radius: 20px;
   color:white;
   font-size: 36px;
   text-align: center;
}
.form input{
    height:40px;
    width:300px;
    border:2px solid red;
    border-radius:20px;
    text-align: center;
    outline: none;
}
.form button{
    height: 40px;
    margin-left: 50px;
    width:80px;
    color:black;
    background:white;
    outline:none;
    border-radius:10px;
}
ul{
    text-align :center;
    list-style: none;
}
ul li{
    margin:10px;
    background:#c0c0c0;
    font-size:36px;
}